import React, { Fragment } from "react";
import { Header, List, ListContent } from 'semantic-ui-react'

export const Experience = () => (
  <Fragment>
    <section>
      <Header as='h3'>EXPERIENCE</Header>
      <List>
        <List.Item className="highlighted">
          <List bulleted>
            <List.Item>
              <ListContent>
                <List.Header>
                  <em>Lviv city council | 01/2017 - Present</em>
                </List.Header>
                <List.Description>
                  Administration of the portal of urban cadastre. Responding to citizens' appeals.
                </List.Description>
              </ListContent>
            </List.Item>

            <List.Item>
              <ListContent>
                <List.Header>
                  <em>Lviv city council | 06/2016 - 01/2017</em>
                </List.Header>
                <List.Description>
                  Checking topographic geodetic surveys. Responding to citizens' appeals
                </List.Description>
              </ListContent>
            </List.Item>
          </List>
        </List.Item>
      </List>
    </section>
  </Fragment>
)
