import React, { Fragment } from "react";
import { Flag, Header, List } from 'semantic-ui-react'

const flagRenderer = countryCode => <Flag name={countryCode} />

export const Languages = () => (
    <Fragment>
        <section>
            <Header as='h3'>Languages</Header>
            <List>
                <List.Item title="Native">
                    {flagRenderer('ua')}
                    <span className="language">–£–ö–†–ê–á–ù–°–¨–ö–ê</span></List.Item>
                <List.Item title="Fluent">
                    {flagRenderer('ru')}
                    <span className="language">–†–£–°–°–ö–ò–ô</span></List.Item>
                <List.Item title="Pre-Intermediate">
                    {flagRenderer('us')}
                    <span className="language">ENGLISH</span></List.Item>
            </List>
        </section>
    </Fragment>
)